.cs-input,
.cs-textarea {
	display: block;
	width: 100%;
	background-color: #F4F6F9;
	margin-bottom: 1.6rem;
	font-weight: normal;
	font-size: 1.6rem;
	line-height: 2.8rem;
	padding: 1rem 1.5rem;
	border: 0;
	border-radius: 0.4rem;

	&::placeholder {
		font-weight: 400;
		font-size: 1.6rem;
		line-height: 2.8rem;
		color: rgba(46, 54, 62, 0.3)
	}
}

.cs-input {
	height: 3.6rem;
}

.cs-textarea {
	height: 10.7rem;
	resize: none;
}

.cs-form__button {
	background: #5D6AFB;
	border-radius: 5px;
	font-weight: bold;
	font-size: 1.4rem;
	line-height: 1.8rem;
	border: 0;
	outline: none;
	color: #fff;
	padding: 1.2rem 2.4rem;
}

.cs-form__action {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	padding-top: 2.6rem;
}

.cs-form__checkbox {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	cursor: pointer;
}

.cs-form__label {
	padding-left: 1rem;
	font-weight: normal;
	font-size: 1.2rem;
	line-height: 1.8rem;
	color: #2E363E;
}

.cs-contacts {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
}

.cs-contacts__title {
	font-weight: bold;
	font-size: 1.4rem;
	line-height: 1.8rem;
	padding-bottom: 1.6rem;
	color: #2E363E;
}

.cs-contacts__email,
.cs-contacts__phone,
.cs-contacts__link-map {
	display: block;
	font-weight: normal;
	font-size: 1.6rem;
	line-height: 2.8rem;
	padding-bottom: 1.6rem;
	color: #2E363E;
	position: relative;
	padding-left: 2.3rem;
	text-decoration: none;

	&::before {
		content: '';
		display: inline-block;
		position: absolute;
		left: 0;
	}
}

.cs-contacts__email::before {
	width: 1.4rem;
	height: 1rem;
	top: 0.9rem;
	background: url('./../images/icon-email.png') no-repeat center center;
}

.cs-contacts__phone::before {
	width: 1.6rem;
	height: 1.6rem;
	top: 0.5rem;
	background: url('./../images/icon-phone.png') no-repeat center center;
}

.cs-contacts__link-map::before {
	width: 0.9rem;
	height: 1.3rem;
	top: 0.7rem;
	background: url('./../images/icon-map.png') no-repeat center center;
}

.cs-contacts__social {
	display: flex;
	flex-flow: row nowrap;
	padding-bottom: 2.5rem;

	& a {
		display: block;
		width: 3.2rem;
		height: 3.2rem;
		cursor: pointer;
		margin-right: 1.6rem;
		opacity: 0.5;

		&:hover {
			opacity: 1;
		}
	}
}

.cs-facebook {
	background: url('./../images/icon-facebook.png') no-repeat center center;
}

.cs-instagram {
	background: url('./../images/icon-instagram.png') no-repeat center center;
}

.cs-linkedin {
	background: url('./../images/icon-linkedin.png') no-repeat center center;
}

.cs-article-contacts {
	position: relative;

	&:after {
		content: '';
		display: inline-block;
		width: 27rem;
		height: 26rem;
		position: absolute;
		bottom: -10.6rem;
		right: -8rem;
		background: url('./../images/article-contacts.png') no-repeat center center;

	}
}