.cs-articles,
.cs-article-success {
	max-width: 118.8rem;
	margin: 0 auto;
	padding-top: 10rem;
}

.cs-article-want,
.cs-article-mentor {
	display: flex;
	flex-flow: row nowrap;
}

.cs-article-want__img {
	flex: 1;
	width: 47.3rem;
	height: 49.3rem;
	background: url('./../images/article-want.png') no-repeat center center;
}

.cs-article-want__text {
	flex: 1;
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	padding-left: 7rem;
}

.cs-article__title {
	padding: 0 0 3.6rem 0;
	font-size: 4.8rem;
	line-height: 6rem;
	font-weight: 500;
	color: #2E363E;
}

.cs-article__description {
	padding: 3.6rem 0 0 0;
	font-size: 2.4rem;
	line-height: 3.2rem;
	font-weight: 400;
	color: #7E868E;
}

.cs-article-mentor__text {
	flex: 2;

	& .cs-article__title {
		padding: 0;
	}

	& .cs-article__description {
		padding-top: 4rem;
		padding-bottom: 4rem;
	}
}

.cs-article-mentor__img {
	flex: 1;
	width: 29.2rem;
	height: 55.5rem;
	background: url('./../images/article-mentor.png') no-repeat center center;
}

.cs-article__button {
	background: #5D6AFB;
	border-radius: 5px;
	font-weight: bold;
	font-size: 1.4rem;
	line-height: 1.8rem;
	border: 0;
	outline: none;
	color: #fff;
	padding: 1.2rem 2.4rem;
}

.cs-article-mentor {
	margin-top: 18.8rem;
	margin-bottom: 18.8rem;
}

.cs-article-student {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	max-width: 118.8rem;
	margin: 0 auto;
	padding-top: 10rem;
	padding-bottom: 9rem;

}

.cs-articles--full {
	background: url('./../images/bg-article-student.png') no-repeat center center;
}

.cs-article-student__img {
	width: 71.4rem;
	height: 58.2rem;
	background: url('./../images/article-student.png') no-repeat center center;
}

.cs-article-student__text {
	& .cs-article__title {
		padding: 0;
		text-align: center;
	}

	& .cs-article__description {
		padding-top: 4rem;
		text-align: center;
	}
}

.cs-article-courses__text {
	& .cs-article__title {
		padding: 0;
	}

	& .cs-article__description {
		padding-top: 4rem;
		padding-bottom: 4rem;
	}
}

.cs-article-success__img {
	height: 34.8rem;
	background: url('./../images/article-success.png') no-repeat center center;
}

.cs-article-success__cards {
	display: flex;
}

.cs-success-card {
	flex: 1;
	background-color: #DBF1FF;
	border-radius: 0.5rem;
	padding: 2.6rem 4rem;

	&:first-child {
		margin-right: 1rem;
	}

	&:last-child {
		margin-left: 1rem;
	}
}

.cs-success-card__description {
	font-weight: normal;
	font-size: 1.6rem;
	line-height: 2.8rem;
	color: #2E363E;
}

.cs-success-card__name,
.cs-success-card__position {
	font-weight: normal;
	font-size: 1.2rem;
	line-height: 1.8rem;
	color: #2E363E;
}

.cs-success-card__name {
	color: #2E363E;
}

.cs-success-card__position {
	color: #7E868E;
}

.cs-article-success {
	padding-bottom: 10rem;
	& .cs-article__title {
		text-align: center;
	}
}

.cs-delimiter {
	height: 0.1rem;
	width: 13rem;
	background-color: #2E363E;
	margin-top: 2rem;
	margin-bottom: 2rem;
}

.cs-article-contacts {
	display: flex;
	flex-flow: row nowrap;
	margin-bottom: 10rem;
}

.cs-contact-form {
	flex: 2;
	margin-right: 17.2rem;
}

.cs-contacts {
	flex: 1;
}

.cs-contact-form__title {
	font-size: 4.8rem;
	line-height: 6rem;
	font-weight: 500;
	letter-spacing: -0.1rem;
	color: #2E363E;
}

.cs-contact-form__description {
	padding-top: 4rem;
	padding-bottom: 4rem;
	font-weight: normal;
	font-size: 2.4rem;
	line-height: 3.2rem;
	color: #7E868E;
}