.cs-header {
	max-width: 192rem;
	margin: 0 auto;
	background: url('./../images/header-img.jpg') no-repeat center center;
}

.cs-header__nav {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	height: 12rem;
	border-bottom: 0.1rem solid rgba(126, 134, 142, 0.1);
	padding-left: 8rem;
	padding-right: 8rem;
}

.cs-nav__logo,
.cs-nav__language-switcher {
	flex: 1;
}

.cs-nav__menu {
	flex: 2;
}

.cs-nav__logo {
	display: flex;
	flex-flow: row nowrap;
	text-decoration: none;
}

.cs-logo__img {
	width: 7.1rem;
	height: 7.1rem;
	background: url('./../images/logo.png') no-repeat center center;
}

.cs-logo__text {
	display: flex;
	align-items: center;
	width: 19.2rem;
	padding-left: 1.7rem;
	font-size: 1.4rem;
	font-weight: 500;
	line-height: 2.1rem;
	color: #2E363E;
}

.cs-menu {
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	margin: 0;
	padding: 0;
	list-style: none;
	color: #2E363E;
}

.cs-menu__item {
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 2.8rem;
}

.cs-menu__item-link {
	text-decoration: none;
	color: #2E363E;
	padding-left: 2rem;
	padding-right: 2rem;
}

.cs-nav__language-switcher {
	display: flex;
	justify-content: flex-end;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 2.8rem;
}

// Header content

.cs-header__content {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	max-width: 118.8rem;
	height: 71rem;
	margin: 0 auto;
}

.cs-header-content {
	max-width: 66rem;
}

.cs-header-content__title {
	padding: 0;
	font-size: 6.4rem;
	line-height: 7.8rem;
	font-weight: 700;
	color: #2E363E;
}

.cs-header-content__description {
	padding-top: 4rem;
	padding-bottom: 4rem;
	font-size: 3.2rem;
	line-height: 4.9rem;
	font-weight: 500;
	color: #2E363E;
}

.cs-header-content__button {
	background: #5D6AFB;
	border-radius: 5px;
	font-weight: bold;
	font-size: 1.4rem;
	line-height: 1.8rem;
	border: 0;
	outline: none;
	color: #fff;
	padding: 1.2rem 2.4rem;
}
