.cs-cards {
	display: flex;
	flex-flow: row wrap;
	margin-left: -12px;
    margin-right: -12px;
	position: relative;
	padding-bottom: 23.8rem;

	&::after {
		content: '';
		display: inline-block;
		width: 20.4rem;
		height: 29.4rem;
		position: absolute;
		right: 0;
		bottom: 5.5rem;
		background: url('./../images/article-cards-illustration.png') no-repeat center center;
	}
}

.cs-card {
	flex-basis: 38rem;
	border-radius: 1rem;
	overflow: hidden;
	border: 0.1rem solid #F2F6FA;
	position: relative;

	filter: drop-shadow(2px 8px 16px rgba(194, 198, 208, 0.12));
	margin: 1.2rem 1.2rem 5.4rem 1.2rem;
	background-color: #fff;
}

.cs-card__header {
	height: 2.6rem;
	background-color: #F2F6FA;
	position: relative;
	z-index: 1;

	&::before {
		content: '';
		position: absolute;
		display: inline-block;
		width: 4.7rem;
		height: 0.5rem;
		background: url('./../images/dots.png') no-repeat center center;
		top: 1.1rem;
		left: 3rem;
		z-index: 2;
	}
}

.cs-card__img {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 22rem;
	border-bottom: 0.1rem solid #F2F6FA;

	& img {
		height: 12.3rem;
		width: auto;
	}
}

.cs-card--react img {
	height: 12.3rem;
}

.cs-card--devops img {
	height: 6.8rem;
}

.cs-card--ux img {
	height: 13.3rem;
}

.cs-card--base img {
	height: 2.2rem;
}

.cs-card--java img {
	height: 11.8rem;
}

.cs-card--java img {
	height: 8.6rem;
}



.cs-card__button {
	width: calc(100% - 6.4rem);
	margin: 3.2rem;
	background-color: transparent;
	color: #5D6AFB;
	border: 0.1rem solid #5D6AFB;
	border-radius: 0.5rem;
	font-weight: bold;
	font-size: 1.4rem;
	line-height: 1.8rem;
	padding: 1.2rem 0;
	cursor: pointer;
}

.cs-card__title {
	margin-top: 3.2rem;
	padding: 0 3.2rem 0 3.2rem;
	height: 8.3rem;
	font-weight: 600;
	font-size: 2.4rem;
	line-height: 3.2rem;
	letter-spacing: -0.1rem;
	color: #2E363E;
}

.cs-card__subtitle {
	font-weight: normal;
	font-size: 1.6rem;
	line-height: 2.8rem;
	color: #7E868E;
	padding: 2.4rem 3.2rem;
}

.cs-card__description {
	font-weight: normal;
	font-size: 2.4rem;
	line-height: 3.2rem;
	color: #2E363E;
	padding: 0 3.2rem;
}