.cs-footer {
	background-color: #5D6AFB;
	margin-top: -2rem;
}

.cs-footer-content {
	max-width: 192rem;
	margin: 0 auto;
	padding-top: 4rem;
	padding-bottom: 4rem;
}

.cs-footer-content__copyright {
	text-align: center;
	font-weight: normal;
	font-size: 1.6rem;
	line-height: 1.8rem;
	padding-top: 4.5rem;
	color: #fff;
}

.cs-footer .cs-menu__item-link {
	color: #fff;
}

.cs-footer-social {
	display: flex;
	flex-flow: row nowrap;
	padding-bottom: 2.5rem;

	& a {
		display: block;
		width: 3.2rem;
		height: 3.2rem;
		cursor: pointer;
		margin-right: 1.6rem;
		opacity: 0.5;

		&:hover {
			opacity: 1;
		}
	}
}

.cs-footer-facebook {
	background: url('./../images/icon-facebook-white.png') no-repeat center center;
}

.cs-footer-instagram {
	background: url('./../images/icon-instagram-white.png') no-repeat center center;
}

.cs-footer-linkedin {
	background: url('./../images/icon-linkedin-white.png') no-repeat center center;
}

.cs-footer-content__nav {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	padding-left: 8rem;
	padding-right: 8rem;
}

.cs-footer-logo {
	font-weight: normal;
	font-size: 1.6rem;
	line-height: 1.8rem;
	text-decoration: none;
	color: #fff;
}